import { r as redirect } from "../../../../chunks/index.js";
const GET = async ({ url, locals: { supabase } }) => {
  if (supabase) {
    const code = url.searchParams.get("code");
    if (code) {
      await supabase.auth.exchangeCodeForSession(code);
    }
  }
  redirect(303, "/profile");
};
export {
  GET
};
